import { Button, Image, Empty } from "antd-mobile";
import Block from './block'

const MyButton = (props) => {
  const config = props.config || {}
  const clickBtn = () => {
    console.log('', config)
    props.sendMqttMsg(config.instructions)
  }
  return (
    <Block config={config}>
      <Button block color="primary" size="large" onClick={clickBtn}>
          {config.name || config.text || ''}
      </Button>      
    </Block>
  )
}
export default MyButton
