import React, { useEffect, useState } from "react";
import { Button, Image, Empty, Toast, Form, Input,Card} from "antd-mobile";
// import * as mqtt2 from 'mqtt'.default
var mqtt2 = require('mqtt')
var clientTmp
var loadingToast
var allContent = ''
/**
 *  host
 *  port
 * 
 * 
 * 
 */
const Demo = () => {
  const [message, setMessage] = useState('')
  const [hasSubcribe, setHasSubcribe] = useState(false)
  function showMsg (str) {
    var now = new Date().toLocaleString()
    Toast.show({
      icon: 'success',
      content: str,
    })
    allContent = `[${now}]${str}` + '\n' + allContent
    setMessage(allContent)
  }
  // eslint-disable-next-line
  mqtt2 = mqtt

  // console.log('mqtt', mqtt2)
  const onConnect = (params) => {
    if(clientTmp) {
      clientTmp.end()
      showMsg('')
      clientTmp = null
      return false
    }
    console.log('connect', params)
    clientTmp = mqtt2.connect(`mqtt://${params.host}`, {
      clientId: params.clientid,
      port: params.port
    })
    loadingToast = Toast.show({
      content: '...',
      duration: 0
    })
    clientTmp.on('connect', function (msg) {
      showMsg('')
      loadingToast.close()
      clientTmp.on('message', function (topic, message) {
        // message is Buffer
        console.log(':', topic, message.toString())
        showMsg( `${topic}\n${message.toString()}`)
      })
    })
  }
  const onListen = (params) => {
    if(clientTmp && clientTmp.subscribe) {
      if(hasSubcribe) {
        clientTmp.unsubscribe(params.topic, function (msg) {
          showMsg(`${params.topic}`)
          setHasSubcribe(false)
        })
      } else {
        clientTmp.subscribe(params.topic, function (msg) {
          showMsg(`${params.topic}`)
          setHasSubcribe(true)
        })
      }
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      })
    }
  }

  const onSendMsg = (params) => {
    if(clientTmp && clientTmp.subscribe) {
      clientTmp.publish(params.topic, params.msg, function (msg) {
        showMsg(`${params.topic}=>${params.msg}`)
      })
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      })
    }
  }

  return (
    <>
    <p></p>
    <p>1websocket1884web1883</p>
    <p>2clientid</p>
    <p>3topic</p>
    <p>4</p>
    <p>5</p>
    <Form
      layout='horizontal' mode='card'
      onFinish={onConnect}
      initialValues={{
        host: window.location.hostname,
        port: 1884,
        clientid: 'web-demo-' + new Date().getTime()
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {clientTmp ? '' : ''}
        </Button>
      }
    >
      <Form.Header>MQTT demo</Form.Header>
      <Form.Item name='host' label='host'>
        <Input placeholder='' />
      </Form.Item>
      <Form.Item name='port' label='port' >
        <Input placeholder='' />
      </Form.Item>
      <Form.Item name='clientid' label='clientid'>
        <Input placeholder='id' />
      </Form.Item>
    </Form>

    <Form
      layout='horizontal' mode='card'
      onFinish={onListen}
      initialValues={{
        topic: 'deviceId-feb20',
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {hasSubcribe ? '...': ''}
        </Button>
      }
    >
      <Form.Header>: id</Form.Header>
      <Form.Item name='topic' label='topic' >
        <Input placeholder='  id' />
      </Form.Item>
    </Form>

    <Form
      layout='horizontal' mode='card'
      onFinish={onSendMsg}
      initialValues={{
        topic: 'deviceId-feb20',
        msg: 'hello world',
      }}
      footer={
        <Button block type='submit' color='primary' size='large'>
          {clientTmp ? '' : ''}
        </Button>
      }
    >
      <Form.Header>: </Form.Header>
      <Form.Item name='topic' label='topic' >
        <Input placeholder='  id' />
      </Form.Item>
      <Form.Item name='msg' label='' >
        <Input placeholder='' />
      </Form.Item>
    </Form>
    <Card title='' >
      <div style={{
        whiteSpace: 'pre-line'
      }}>
      {message}
      </div>
    </Card>
    </>
  )
}
export default Demo
