import { Button, Image, Empty, Toast, Form,
  Input,} from "antd-mobile";
import ButtonBlock from '../components/button';
import ImageBlock from '../components/image';
import {apiGetQrDetail, apiGetQrDetailBy, apiGetQrDetailBySec} from '../../utils/apis'
import {getValueFromUrl, formatTime} from '../../utils/utils'
import style from './index.module.scss'
import { useEffect, useState } from "react";

// import * as mqtt from "mqtt"  // import everything inside the mqtt module and give it the namespace "mqtt"
// let client = mqtt.connect('mqtt://test.mosquitto.org') // create a client

// import { connect } from "mqtt"  // import connect from mqtt
// const {connect} = require('mqtt')
// let client = connect('mqtt://test.mosquitto.org') // create a client
var client = {}
const Detail = () => {
  const [detail, setDetail] = useState({}) // 
  const [showPedInput, setShowPasswordInput] = useState(false);
  // const [client, setClient] = useState({})

  useEffect(()=> {
    getDetailData()
  }, [])
  const getDetailData = () => {
    const id = getValueFromUrl('id') // URLid
    console.log('url id params', id)
    if(id) {
      const obj = {id}
      apiGetQrDetail(obj).then(res => {
        if(res.code === 0) {
          if(res.data.needPassword) {
            // 
            setShowPasswordInput(true)
          } else {
            setDetail(res.data)
            if(res.data.deviceId){
              mqttInit({...res.data})
            }            
          }
        } else {
          Toast.show({
            icon: 'fail',
            content: '',
          })
        }
      })
    } else {
      Toast.show({
        icon: 'fail',
        content: '',
      })
    }
  }
  const mqttInit = (options) => {
    var {deviceId, mqttDomain} = options
    const domainNow = window.location.hostname
    mqttDomain = mqttDomain || `mqtt://${domainNow}`
    const timeStramp = new Date().getTime()

    const clientId = deviceId + '-' + timeStramp
    console.log('clientId', clientId)
    // eslint-disable-next-line
    let clientTmp = mqtt.connect(mqttDomain, {
      clientId: clientId,
      port: 1884
    })
    clientTmp.on('connect', function (msg) {
      console.log('', msg)
      // setClient(clientTmp)
      client = clientTmp

      clientTmp.on('message', function (topic, message) {
        // message is Buffer
        console.log(':', topic, message.toString())
        // client.end()
      })
      clientTmp.subscribe(deviceId, function (topic, message) {
        // message is Buffer
        console.log(`${deviceId}:`, topic, message)
        // if(topic) {
        //   Toast.show({
        //     icon: 'success',
        //     content: '',
        //   })          
        // }
        // client.end()
      })
      clientTmp.subscribe('deviceId-xxx1', function (topic, message) {
        // message is Buffer
        console.log(`deviceId-xxx1:`, topic, message)
        // client.end()
      })
    })
  }
  const sendMqttMsg = (msg) => {
    console.log('',detail.deviceId, msg)
    client.publish(detail.deviceId, msg || 'hello, I am' + detail.deviceId)
  }

  // 
  const onPwdFinish = (values) => {
    console.log('', values)
    const id = getValueFromUrl('id') // URLid
    const obj = {
      id,
      ...values
    }
    apiGetQrDetailBySec(obj).then(res => {
      if(res.code === 0) {
        if(res.data.company) {
          // 
          setShowPasswordInput(false)

          setDetail(res.data)
          if(res.data.deviceId){
            mqttInit({...res.data})
          }
        }
      } else {
        Toast.show({
          icon: 'fail',
          content: '' + res.msg,
        })
      }
    })
  }
   
  const dom = showPedInput === true ? (
    <div className={style.wrap}>
      <h1 className={style.h1}></h1>
      <Form
        onFinish={onPwdFinish}
        footer={
          <Button block type='submit' color='primary' size='large'>
            
          </Button>
        }
      >
        <Form.Item name='secret' label='' rules={[{ required: true }]}>
          <Input placeholder=''  type='password'/>
        </Form.Item>
      </Form>
    </div>
  ) :
      (<div className={style.wrap}>
        <h1 className={style.h1}>{detail.title}</h1>
        <p className={style.source}>
          <span className={style.sourcename}>{detail.company}</span>
          <span className={style.time}>{formatTime(detail.createTime || detail.updateTime)}</span>
        </p>
        <p className={style.desc}>
          {detail.desc || ''}
        </p>
        {
          detail.config && detail.config.map((item, idx) => {
            switch(item.type) {
              case 'button':
                return <ButtonBlock key={idx} config={item} sendMqttMsg={sendMqttMsg}/>
              case 'image':
                return <ImageBlock key={idx} config={item} />
              default:
                return <Empty/>
            }
          })
        }
      </div>)
  return dom
};
export default Detail;
